import { NavLink } from "react-router-dom";
import Switch from "../../Switch";
import Button from '@mui/material/Button';
import { makeStyles } from "@material-ui/core/styles";

import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css'

//componentes
import {
  Box,
  Divider,
  Typography,
  List,
  ListItem,
  ListItemText,
} from "@material-ui/core";
const useStyles = makeStyles((theme) => ({
  link: {
    margin: theme.spacing(1, 1),
    color: "black",
  },
}));
/**
 * Componente con los botones para el sidebar 
 * @param {*} props 
 * @returns Botones para el sidebar dependiendo si es admin o no
 */
const DrawerS = (props) => {
  const classes = useStyles();


    if (props.user.is_superuser) {
        return (
            <Box onClick={props.handleDrawerToggle} sx={{ textAlign: 'center' }}>
              <a className={classes.link} component={NavLink} href="/">
              <Button sx={{ color: '#fff' }} >
               <LazyLoadImage effect="blur"  src={'http://sccotview.com/media/videos/logoSCCOTView.png'} alt="" className="" style={{ width:"220px", objectFit:'contain',minHeight:"2px" }} />
              </Button>
              </a>
              <Divider />
              <Box
                display="flex"
                mt={2}
                mr={2}
                justifyContent="center"
                fontWeight={500}
              >
                <Typography component={"span"} variant={"body2"}>
                  <Box ml={2} fontWeight="fontWeightLight" fontSize={14}>
                    <Switch></Switch> 
                  </Box>
                  <Box ml={2} fontWeight="fontWeightLight" fontSize={14} className="mb-4">
                    {props.user.name}
                  </Box>
                  
                  <div className="d-flex flex-column">

                  <a className={classes.link} component={NavLink} href="/">
                    <Button sx={{ color: '#fff' }} >
                      INICIO
                    </Button>
                  </a>
                  <a className={classes.link} component={NavLink} href="/Videos">
                    <Button sx={{ color: '#fff' }} >
                      VIDEOS
                    </Button>
                  </a>
                  <a className={classes.link} component={NavLink} href="/Series">
                    <Button sx={{ color: '#fff' }} >
                      SERIES
                    </Button>
                  </a>
                  <a className={classes.link} component={NavLink} href="/Casos">
                    <Button sx={{ color: '#fff' }} >
                      CASOS
                    </Button>
                  </a>
                  <a className={classes.link} component={NavLink} href="/Ajustes">
                    <Button sx={{ color: '#fff' }} >
                      AJUSTES
                    </Button>
                  </a>
                  <a className={classes.link} component={NavLink} href="/VideoForm">
                    <Button sx={{ color: '#fff' }} >
                      AGREGAR VIDEO
                    </Button>
                  </a>
                  </div>

                </Typography>        
              </Box>
            </Box>
            );
    } else {
        return (
          <Box onClick={props.handleDrawerToggle} sx={{ textAlign: 'center' }}>
              <div >
                <img  src="http://sccotview.com/media/videos/logoSCCOTView.png" style={{ width: "170px", objectFit:'contain',minHeight:"2px"}} />
              </div>
              <Divider />
              <Box
                display="flex"
                mt={2}
                mr={2}
                flexDirection={"column"}
                alignItems={"center"}
                justifyContent={"center"}
                fontWeight={500}
              >
                <Typography component={"span"} variant={"body2"}>
                  <Box ml={2} fontWeight="fontWeightLight" fontSize={14}>
                    <Switch></Switch> 
                  </Box>
                  <Box ml={2} fontWeight="fontWeightLight" fontSize={14}>
                    {props.user.name_odoo}
                  </Box>
                  <div className="d-flex flex-column">
                  <a className={classes.link} component={NavLink} href="/">
                    <Button sx={{ color: '#fff' }} >
                      INICIO
                    </Button>
                  </a>
                  <a className={classes.link} component={NavLink} href="/Videos">
                    <Button sx={{ color: '#fff' }} >
                      VIDEOS
                    </Button>
                  </a>
                  <a className={classes.link} component={NavLink} href="/Series">
                    <Button sx={{ color: '#fff' }} >
                      SERIES
                    </Button>
                  </a>
                  <a className={classes.link} component={NavLink} href="/Casos">
                    <Button sx={{ color: '#fff' }} >
                      CASOS
                    </Button>
                  </a>
                  </div>

                </Typography>        
              </Box>
            </Box>
            );
    }
    
};

export default DrawerS ;