//components
import { useState } from 'react';
import Button from '@mui/material/Button';
import { NavLink } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import {  useContext } from "react";
import NotificationsIcon from '@mui/icons-material/Notifications';

//dependencias
import useUser from '../../hooks/useUser'
import Context from "../context/UserContext";
import NotificationsDropdown from './../notifications/notifications_navbar';

const useStyles = makeStyles((theme) => ({
  link: {
    margin: theme.spacing(3, 1),
    color: "black",
  },
}));
/**
 * Componente con botones para el nabvar
 * @returns Botones para el nabvar dependiendo si es admin o no
 */

const NavbarAd = () => {
  
  const { isLogged } = useUser()
  const { user } = useContext(Context)
  const classes = useStyles();
  const [isOpen, setIsOpen] = useState(false);
  const [notifications, setNotifications] = useState([]);

  const handleNotificationIconClick = () => {
    setIsOpen(!isOpen);
  };
  const dummyNotifications = [
    { id: 1, text: 'Notificación 1' },
    { id: 2, text: 'Notificación 2' },
    { id: 3, text: 'Notificación 3' },
  ];
  return (
    <>
      {isLogged && user.is_superuser  &&
      <>
      {/* aqui es necesario ingresar restablecer  */}
        <a className={classes.link} component={NavLink} href="/">
          <Button sx={{ color: '#fff' }} >
            INICIO
          </Button>
        </a>
        <a className={classes.link} component={NavLink} href="/Videos">
          <Button sx={{ color: '#fff' }} >
            VIDEOS
          </Button>
        </a>
        <a className={classes.link} component={NavLink} href="/Series">
          <Button sx={{ color: '#fff' }} >
            SERIES
          </Button>
        </a>
        <a className={classes.link} component={NavLink} href="/Casos">
          <Button sx={{ color: '#fff' }} >
            CASOS
          </Button>
        </a>
        <a className={classes.link} component={NavLink} href="/Ajustes">
          <Button sx={{ color: '#fff' }} >
            AJUSTES
          </Button>
        </a>
        <a className={classes.link} component={NavLink} href="/VideoForm">
          <Button sx={{ color: '#fff' }} >
            AGREGAR VIDEO
          </Button>
        </a>
      </>
      }
      {isLogged && !user.is_superuser  &&
        <div className=''>
        <a className={classes.link} component={NavLink} href="/">
          <Button sx={{ color: '#fff' }} >
            INICIO
          </Button>
        </a>
        <a className={classes.link} component={NavLink} href="/Videos">
          <Button sx={{ color: '#fff' }} >
            VIDEOS
          </Button>
        </a>
        <a className={classes.link} component={NavLink} href="/Series">
          <Button sx={{ color: '#fff' }} >
            SERIES
          </Button>
        </a>
        <a className={classes.link} component={NavLink} href="/Casos">
          <Button sx={{ color: '#fff' }} >
            CASOS
          </Button>
        </a>
        </div>
      } 
    </>
  )
}  

export default NavbarAd;
